<template>
  <!-- <div class="text-center my-3">
    <span class="fs-1 fw-bold text-white bg-brown opacity-75 px-5">全部商品</span>
  </div> -->
  
  <swiper
    class="mt-5"
    :style="{
      '--swiper-navigation-color': 'brown',
      '--swiper-pagination-color': 'brown',
    }"
    :modules="modules"
    :navigation="true"
    :autoplay="{ delay: 2500, disableOnInteraction: false, }"
    :breakpoints="{
      '576': {
        slidesPerView: 1,
        spaceBetween: 10,
      },
      '768': {
        slidesPerView: 2,
        spaceBetween: 10,
      },
      '1024': {
        slidesPerView: 3,
        spaceBetween: 10,
      },
    }"
  >
    <swiper-slide v-for="product in products">
      <div>
        <div class="card border mb-4 col-sm-12">
          <div @click="getDetail(product.id)">
            <img :src="product.imageUrl" class="card-img-top object-fit" />
          </div>
          <div class="card-body p-0 p-2">
            <div class="d-flex justify-content-between align-items-center my-2">
              <span class="fw-bold fs-4">
                {{ product.title }}
              </span>
              <span class="badge pill bg-brown">{{ product.category }}</span>
            </div>
            <div class="d-flex justify-content-between align-items-center">
              <span class="fs-5 text-danger fw-bold"
                >特價 NT${{ $filters.currency(product.price) }}</span
              >
              <span class="text-muted"
                ><del
                  >原價NT${{ $filters.currency(product.origin_price) }}</del
                ></span
              >
            </div>
          </div>
        </div>
      </div>
    </swiper-slide>
  
  </swiper>
</template>
<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

// import required modules
import { Autoplay, Pagination, Navigation } from "swiper";

export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  emits: ["update"],
  data() {
    return {
      modules: [Pagination, Autoplay, Navigation],
      products: [],
    };
  },
  methods: {
    getProducts() {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`;

      this.$http.get(url).then((res) => {
        // console.log("all", res);
        this.products = res.data.products;
      });
    },
    getDetail(id) {
      this.$router.push(`/product/${id}`);
      this.$emit("update");
    },
  },
  mounted() {
    this.getProducts();
  },
};
</script>

