<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="container">
      <div v-if="followProduct.length > 0">
        <h3 class="mt-5">收藏商品</h3>
        <div class="table-responsive my-5 fs-5">
          <table class="table text-center border align-middle">
            <thead class="table-tan  ">
              <tr >
                <th class="text-white">品名</th>
                <th class="text-white">分類</th>
                <th class="text-white">價格</th>
                <th class="text-white">購物車</th>
                <th class="text-white">取消</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in followProduct" :key="item.id">
                <td >
                  <router-link :to="`/product/${item.id}`" style="color: black;text-decoration: none;">
                    <img :src=item.imageUrl class="object-fit" style="height: 120px;">
                      <p class="fw-bold">{{ item.title }}</p>
                  </router-link>
                </td>
                <td>{{ item.category }}</td>
                <td>{{ item.price }}</td>
                <td>
                  <button
                    type="button"
                    class="btn btn-outline-danger fs-5"
                    @click.prevent="addCart(item.id)"
                  >
                    <i class="bi bi-cart-fill"></i> 加入購物車
                  </button>
                </td>
                <td>
                  <button
                    href="#"
                    class="btn btn-outline-secondary"
                    @click.prevent="delFollowed(item.id)"
                  >
                  <i class="bi bi-x-lg "></i>
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!-- <div v-else>
        <div class="d-flex justify-content-center my-7">
          <h4 class="mb-3 warning-text">沒有追蹤商品，快去逛逛</h4>
          <router-link to="/products" class="text-dark mt-5 mt-3 h5">
            <i class="fas fa-chevron-left mr-2"></i>
            查看商品
          </router-link>
        </div>
      </div> -->

      <div v-else class="container">
        <div class="row mt-5">
          <div
            class="col-md-8 border p-5 rounded bg-brown text-white text-center mx-auto"
          >
            <h1>收藏為空</h1>
            <router-link
              to="/products"
              class="btn btn-brown border btn-md-lg w-100 mt-5"
            >
              繼續購物
            </router-link>
          </div>

          <div class="row mt-5">
            <div class="text-center my-3">
              <span class="fs-1 fw-bold text-white bg-brown opacity-75 px-5"
                >熱銷商品</span
              >
            </div>
            <div class="container-fluid">
              <AllProduct />
            </div>
          </div>
        </div>
      </div>


    </div>
  </div>
</template>

<script>
import Toast from '@/utils/Toast';
import emitter from '@/methods/emitter';
import AllProduct from '@/components/AllProduct.vue';
export default {
  components:{
    AllProduct
  },
  data() {
    return {
      products: {},
      carts: {},
      followProduct: {},
      followed: JSON.parse(localStorage.getItem("followList")) || [],
      isLoading: false,
      id: [],
    };
  },
  inject: ["emitter", "$httpMessageState"],
  created() {
    this.getProducts();
  },

  methods: {
    getProducts() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products`;
      this.isLoading = true;
      this.$http
        .get(url)
        .then((res) => {
          this.products = res.data.products;
          this.getFollow();
          this.isLoading = false;
        })
        .catch(() => {
          this.isLoading = false;
        });
    },
    getFollow() {
      this.followProduct = this.products.filter(
        (product) => this.followed.indexOf(product.id) > -1
      );
      //   console.log("followProduct", this.followProduct);
    },
    addCart(id) {
      this.isLoading = true;
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      const cart = {
        product_id: id,
        qty: 1,
      };
      this.$http.post(url, { data: cart }).then((res) => {
        this.isLoading = false;
        this.message = res.data.message;
        Toast.fire({
          icon: 'success',
          title: this.message,
        });
        emitter.emit("get-cart");
      });
    },
    editfollow(id) {
      const followId = this.followed.indexOf(id);
      if (followId === -1) {
        this.followed.push(id);
        // console.log("取消");
      } else {
        this.followed.splice(followId, 1);
        // console.log("收藏");
      }
      localStorage.setItem("followList", JSON.stringify(this.followed));
    },
    delFollowed(id) {
      const followId = this.followed.indexOf(id);
      if (followId !== -1) {
        this.followed.splice(followId, 1);
        Toast.fire({
          icon: 'error',
          title: '取消收藏',
        });
        this.getFollow();
      }
      localStorage.setItem("followList", JSON.stringify(this.followed));
    },
  },
  created() {
    this.getProducts();
  },
};
</script>
